import React from 'react'
import "./Style.css";
import data from './productData';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useParams } from 'react-router-dom'
import { addItem } from './store'
import { addCount, subCount } from './store';
import { useDispatch, useSelector } from 'react-redux'; 
import { useState } from 'react';
import { Button } from 'react-bootstrap';

export default function Detail() {

  const [best] = useState(data)
  const {id} = useParams()
  const state = useSelector((state) => state)
  const dispatch = useDispatch()

  return (
    <>
    <div className='infoBox'>
      <img src={best[id].image} alt="bestProduct" style={{width: 600}}></img>
      <div className='productInfo kor'>
        <h6 className='engA' style={{color: "#61605e", paddingBottom: 10}}>{best[id].eng}</h6>
        <h3 style={{color: "#3d7c69"}}>{best[id].title}</h3>
        <h6 style={{color: "#61605e", paddingBottom: 20}}>{best[id].desc}</h6>
        <p style={{paddingBottom: 10, fontWeight: "bold"}}>{best[id].price}</p>

        <div className='detailCount'>
          <button onClick={() => dispatch(subCount(state.best.id))}><i class="fas fa-minus"></i></button>
          <input id="quantity" name="quantity_opt[]" type="text" value="1"></input>
          <button onClick={() => dispatch(addCount(state.best.id))}><i class="fas fa-plus"></i></button>
        </div>
        <div className='line'></div>
      
        <Button className='cartBtn' onClick={() => { 
          dispatch(
            addItem({
              id: best[id].id, 
              image: best[id].image,
              title: best[id].title, 
              price: best[id].price, 
              count: 1
            }))
          }} style={{marginBottom: 30}}>장바구니</Button>{' '}
      </div>
    </div>

    <div style={{width: "100%", marginTop: 40}}>
      <img src={best[id].detail} alt="bestProduct_detail" style={{width: 900, margin: "50px auto 90px"}}></img>
    </div>
    </>
  )
}
