import { Swiper, SwiperSlide } from "swiper/react"; // basic
import { Autoplay, Navigation, Pagination, EffectCoverflow } from 'swiper/modules';
import "swiper/css"; //basic
import "swiper/css/effect-coverflow";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "./Style.css";


export default function MainSwiper () {

  return (
    <Swiper
      className="MainSwiper"
      modules={[Autoplay, Navigation, Pagination, EffectCoverflow]} // 모듈추가
      navigation
      pagination={{ clickable: true }}
      autoplay={{ delay: 4500, disableOnInteraction: false }}
      loop={true}
      effect={"coverflow"}
      grabCursor={true}
      centeredSlides={true}
      slidesPerView={"auto"}
      coverflowEffect={{
        rotate: 0,
        stretch: 80,
        depth: 200,
        modifier: 1,
        slideShadows: false,
      }}
    >
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_rosemary.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_neroli.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_teatree.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_rose.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_marjoram.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_lavender.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_patchouli.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_lemongrass.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_frankincense.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_peppermint.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_ginger.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_juniperBerry.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_grapeFruit.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_eucalyptus.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_lemon.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide>
      {/* <SwiperSlide className="SwiperSlide">
        <img src={process.env.PUBLIC_URL + '/images/ingredi_orange.png'} alt="main visual" style={{width: "100%"}}/>
      </SwiperSlide> */}
    </Swiper>


  );
};


