import React from "react";
import "./Style.css";
import ReactPlayer from 'react-player/lazy';

export default function Circle() {
  return (
    <>
      <div className="joinTheCircle kor">
        <h6 className="plastic">플라스틱이 쓰레기가 되지 않고 무한하게 다시 태어나는 세상,</h6>
        <h6>원형으로 순환되는 삶을 위한 아로마티카의 제안</h6>
        <ReactPlayer
          className="circleVideo"
          url={process.env.PUBLIC_URL + '/images/joinTheCircle.mp4'}
          width='440px'
          height='440px'
          playing={true}
          muted={true}
          controls={false}
          loop={true}
        />
        <h1 className="join engA">JOIN THE CIRCLE, RECYCLE WITH US</h1>
        <p style={{width: 810}}>아로마티카는 공병과 투명 페트를 모읍니다. 지역마다 거점을 세워 공병 수거함을 설치하고 전기트럭으로 여기에 모은 자원을 직접 수거합니다. 이렇게 모은 투명 페트로 아로마티카의 제품 용기와 굿즈를 만들죠. 수도권의 제로웨이스트숍들부터 시작해 생활 공동체, 상업 시설, 지역자치단체들과 협력해 더 많은 자원을 수거합니다. 선형이 아닌 원형의 순환 시스템을 만드는 아로마티카의 큰 그림.<br/>더 나은 세상을 위한 JOIN THE CIRCLE에 합류하세요.</p>
      </div>
      <div className="recycle kor">
        <img className="symbol" src={process.env.PUBLIC_URL + '/images/aromatica-symbol.png'} alt="aromaticaSymbol" style={{width: 350}}></img>

        <p style={{width: 760}}>조인 더 서클은 플라스틱이 더 이상 쓰레기가 되지 않고 본래의 형태로 다시 태어나는, 즉 플라스틱 자원이 무한 순환되는 삶을 함께 만들어가자는 의미를 담고 있습니다. 이는 기존에 운영되던 환경 캠페인인 ‘무한 플라스틱 사이클’을 확장한 것으로 아로마티카는 앞으로 조인 더 서클이라는 새로운 이름으로 플라스틱 자원 순환 시스템을 알리고, 나아가 아로마티카의 지속가능한 라이프스타일에 동참을 제안하는 캠페인을 전개합니다.</p>

        <img className="campaign" src={process.env.PUBLIC_URL + '/images/joinCampaign.jpg'} alt="joinCampaign" style={{width: "100%"}}></img>
      </div>

      <div className="goal kor">
        <div className="goalBox">
          <div>
            <img className="fake" src={process.env.PUBLIC_URL + '/images/fake.jpg'}   alt="fake" style={{width: 300}}></img>
            <h3>가짜 재활용.</h3>
            <h6>국내 재활용 선별장의 문제점.</h6>
            <p style={{width: 300}}>자원을 종류별로 열심히 분리배출해도 재활용  선별장에서 이들은 대부분 뒤섞이고 오염됩니다. 더러운 바닥, 좁은 공간, 열악한   작업 환경 속에 작업자들이 수동으로 선별을 하는 과정에서 제대로 선별되지 못한  공병들은 모두 잔재 쓰레기가 되어 소각되거나 매립되어버리죠. 게다가 시중의  화장품 공병은 대부분 여러 소재가 섞여 제작되므로 대부분 선별되지 못하고 잔재   쓰레기가 됩니다.</p>
          </div>

          <div>
            <img className="fake" src={process.env.PUBLIC_URL + '/images/pet.jpg'}  alt="fake" style={{width: 300}}></img>
            <h3>투명 페트.</h3>
            <h6>아로마티카가 투명 페트에 주목하는 이유.</h6>
            <p style={{width: 300}}>
              PET, PP, PS. PVC… 수많은 플라스틱 중 가장 고품질로 재활용 가능한 것이   페트입니다. 선별 과정을 지나 분쇄하고 녹이는 재활용 공정을 거친 페트는  새로운 화장품 용기를 만드는 원료가 됩니다. 더욱 활용 가치가 높은 재활용  원료를 만들기 위해 우리는 페트 중에서도 다른 소재가 섞이지 않은 투명한   페트만을 모읍니다. 발수 코팅과 같은 다른 공정이 더해진 페트는 재활용  원료로서의 가치가 떨어지니까요. 우리가 연대한 수거 거점을 통해 모인 투명   페트는 공정을 통해 최상급 PCR(Post-Consumer-Recycled)플라스틱으로 다시  태어납니다.</p>
          </div>

          <div>
            <img className="fake" src={process.env.PUBLIC_URL + '/images/10t.jpg'}  alt="fake" style={{width: 300}}></img>
            <h3>10톤.</h3>
            <h6>우리의 목표이자 시작.</h6>
            <p style={{width: 300}}>수거 거점을 통해 모인 투명 페트를 분쇄해 재활용   원료인 플레이크로 만들 때 필요한 최소한의 배치수량이 10톤입니다. 10톤이   모이지 않으면 재활용 원료로서의 가치가 떨어지는 플라스틱을 섞어야 합니다.   우리는 직접 모은 깨끗한 자원으로 A급의 재활용 플라스틱을 생산하기 위해  10톤이라는 목표를 향해 나아갑니다. 느리고 번거롭고 어렵더라도, 그곳에서부터  진짜 재활용이 시작될 수 있기 때문입니다.
              </p>
          </div>
        </div>
      </div>

      <div className="planet">
        <div className="planetBox">
          <ReactPlayer
            className="earth"
            url={process.env.PUBLIC_URL + '/images/earth.mp4'}
            width='370px'
            height='370px'
            playing={true}
            muted={true}
            controls={false}
            loop={true}
          />
          <div className="txt kor">
            <h2 className="join engA">SAVE THE PLANET.</h2>
            <h6>아로마티카의 지구를 위한 선한 영향력.</h6>
            <p style={{width: 510}}>아로마티카는 생명과 환경을 지키는 지속가능하고 효과적인 제품을 만듭니다. 지구 환경을 온전한 상태로 되돌리기 위해 환경 보전과 복원에 적극적으로 나섭니다. 완전한 순환과 재활용을 통해 지구에 쓰레기를 남기지 않겠다는 최종 목표를 가지고 있습니다. 우선 전 제품에 재활용 플라스틱, 유리 소재를 적용하여 탄소배출을 절감하고 재활용이 용이한 지속가능한 패키지를 개발, 적용 했습니다. 또한 투명 페트 재활용 시스템을 통해 국가적 자원순환 시스템의 모델이 될 수 있도록 방향을 제시하며 원형적 순환 문화가 정착하도록 기여합니다.</p>
          </div>
        </div>
      </div>
    </>
    
  )
}