import React from 'react';
import "./Style.css";
import Table from 'react-bootstrap/Table';
import { useDispatch, useSelector } from 'react-redux'; 
import { deleteItem, addCount, subCount } from './store';

export default function Cart() {
  const state = useSelector((state) => state)
  const dispatch = useDispatch()
  
  return (
    <div className='cart'>
      <div className='cartBox'>
        <h1 className='engB'>CART</h1>
        <div className='product'>
          <h4 className='engB'>Product</h4>
          <div className='line'></div>

          <Table className='cartTable'>
            <tbody className='tbody kor'>
              {
                state.cart.map((item, i) => {
                  return (
                  <tr className='tBox' style={{width: 100, height: "auto"}} key={i}>
                    <td>
                      <img src={state.cart[i].image} alt="product visual" style={{width: 100}}/>
                    </td>
                    <td className='cartTitle'>{state.cart[i].title}</td>
                    <td>{state.cart[i].price}</td>
                    <td className='count'>
                      <button onClick={() => dispatch(subCount(state.cart[i].id))}><i class="fas fa-minus"></i></button>
                      <div>{state.cart[i].count}</div>
                      <button onClick={() => dispatch(addCount(state.cart[i].id))}> <i class="fas fa-plus"></i></button>
                    </td>
                    <td>
                      <button className='deleteBtn' onClick={() => dispatch(deleteItem(state.cart[i].id))}><i class="fas fa-times"></i></button>
                    </td>
                 </tr>
                  )
                })
              }
            </tbody>
          </Table>   
          <div className='line'></div>
        </div>

        {/* <div className='total'>
          <h4 className='engB'>Total</h4>
          <div className='line'></div>

          <div className='line'></div>
        </div> */}
      </div>
    </div>   
  )
}


