import React from "react";
import "./Style.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import data from './productData';
import Detail from './Detail';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Routes, Route, Link } from 'react-router-dom';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addItem } from './store';

export default function Best() {
  const dispatch = useDispatch()
  const [best] = useState(data)
  // const bestHome = [best].slice(0,3)
  const state = useSelector((state) => state)
  
  return (
    <>
    <Routes>
    <Route path='best' element={
    <Container style={{width: "100vw"}}>
            <img src={process.env.PUBLIC_URL + '/images/shop_013.png'} alt="main visual" style={{width: "100%"}}/>
            <h1 style={{padding: 30}}>BEST</h1>
          <Row>
            {
              best.map((best, index) => {
                return (
                  <Col key={index}>
                    <Link to={`detail/${index}`}>
                      <img src={best.image} alt="product_img" style={{width: 400}}/>
                      <h6 style={{padding: 10}}>{best.title}</h6>
                      <p style={{fontSize: 12}}>{best.desc}</p>
                      <p>{best.price}</p>
                    </Link>
                    <Button variant="dark"  onClick={() => { 
                      dispatch(
                        addItem({
                          id: best.id, 
                          image: best.image,
                          title: best.title, 
                          price: best.price, 
                          count: 1
                        }))
                    }} style={{marginBottom: 30}}>장바구니</Button>{' '}
                  </Col>
                )
              })
            }
          </Row>
        </Container>
    }>
    </Route>
      <Route path='detail/:id' element={<Detail best={best}/>}></Route>
      </Routes>
    </>
  )
}
