import React from "react";
import "./Style.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { useNavigate } from 'react-router-dom';
import { Container, Nav, Navbar } from 'react-bootstrap';

export default function Header () {
  const navigate = useNavigate()

  return (
    <header className="header">
      <Navbar bg="light" data-bs-theme="light">
        <Container className="engA">
          <Navbar.Brand className="navBrand" onClick={() => {navigate('/')}}>AROMATICA</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link className="brand navMenu" onClick={() => {navigate('/brand')}}>BRAND</Nav.Link>
            <Nav.Link className="circle navMenu" onClick={() => {navigate('/circle')}}>JOIN THE CIRCLE</Nav.Link>
            <Nav.Link className="shop navMenu" onClick={() => {navigate('/shop')}}>SHOP</Nav.Link>
          </Nav>

          <Nav className="navCart">
            <Nav.Link className="cart" onClick={() => {navigate('/cart')}}>
              CART
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    </header>
  )
}

