import React from "react";
import "./Style.css";
import styled from 'styled-components';

import { useState } from 'react';

const TabMenu = styled.ul`
  color: #c3cac8;
  font-weight: bold;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
  margin: 10px 0;
  cursor: pointer;

  .submenu {
    width: 50%;
    padding: 10px;
    font-size: 15px;
    transition: 0.5s;
  }

  .focused {
    color: #f3f0eb;
  }
`;

const Line = styled.div`
  width: 80%;
  height: 1px;
  background-color: #fff;
  margin: 0 auto;
`;

const About = styled.div`
  text-align: center;
  font-size: 15px;

  & p.address {
    margin: 40px 0 5px;
  }

  & p.time {
    margin-bottom: 30px;
  }
`;


export default function Tab () {
  // Tab Menu 중 현재 어떤 Tab이 선택되어 있는지 확인하기 위한 currentTab 상태와 currentTab을 갱신하는 함수가 존재해야 하고, 초기값은 0.
  const [currentTab, clickTab] = useState(0);

  const houseArr = [
    { name: 'HOUSE OF AROMATICA 가로수길', 
      address: '서울 강남구 강남대로 162번길 41-4',
      time: '매일 11:00 - 19:00',
      image: process.env.PUBLIC_URL+'/images/house_01.jpg',
    },
    { name: '스타필드 하남점', 
      address: '경기 하남시 미사대로 750 스타필드 하남 1층',
      time: '매일 11:00 - 19:00',
      image: process.env.PUBLIC_URL+'/images/hanam_01.jpg',
    }
  ];

  const selectMenuHandler = (index) => {
    // parameter로 현재 선택한 인덱스 값을 전달해야 하며, 이벤트 객체(event)는 쓰지 않는다
    // 해당 함수가 실행되면 현재 선택된 Tab Menu 가 갱신.
    clickTab(index);
  };

  return (
    <>
      <div className="kor">
        <TabMenu>
         {houseArr.map((el,index) => (
             <li className={index === currentTab ? "submenu focused" : "submenu" }
             onClick={() => selectMenuHandler(index)}>{el.name}</li>
          ))}
        </TabMenu>
        <Line/>
        <About>
          <p className="address">{houseArr[currentTab].address}</p>
          <p className="time">{houseArr[currentTab].time}</p>
          <img className="storeImage" src={houseArr[currentTab].image} alt="storeImage" style={{width: "80%", margin: "0 auto 60px"}}></img>
        </About>
      </div>
    </>
  );
};